@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Quicksand"; 
    
}
.navbar{
  background: #fff  !important; 
}
.navbar-brand{
  font-size: 1.9rem !important;
  color:#565387  !important;

}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.menu-active{
  font-weight: bold;
  border-bottom: 1px solid orangered;
}
.navbar a:hover{
  color:orangered !important;
}

#header{
  width: 100%;
  height: 100vh;
  margin-top: 50px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700px;
  color: black;
}
#header .brand-name{
  color:#3498db;

}
#header h2{
  color: #484848;
  font-size: 24px;
}
#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;

  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color:#3498db;
  border:2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-started:hover{
  background: #3498db;
  color:#fff;
}
#header-img{
  text-align: right;
}
#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  }
 100% {
    transform: translateY(-10px);
  }

}
@keyframes   up-down {
  0% {
    transform: translateY(10px);
  }
 100% {
    transform: translateY(-10px);
  }

}

footer p{
  padding: 0;
  margin: 0;
}

/* responsive code */

@media (max-width:991px){
  #header{
    height:80vh;
    text-align: center;
  }
  #header .header-img{
    text-align: center;
  }
}

