@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Quicksand"; 
    
}
.navbar{
  background: #fff  !important; 
}
.navbar-brand{
  font-size: 1.9rem !important;
  color:#565387  !important;

}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.menu-active{
  font-weight: bold;
  border-bottom: 1px solid orangered;
}
.navbar a:hover{
  color:orangered !important;
}

#header{
  width: 100%;
  height: 100vh;
  margin-top: 50px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700px;
  color: black;
}
#header .brand-name{
  color:#3498db;

}
#header h2{
  color: #484848;
  font-size: 24px;
}
#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;

  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color:#3498db;
  border:2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-started:hover{
  background: #3498db;
  color:#fff;
}
#header-img{
  text-align: right;
}
#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
 100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }

}
@keyframes   up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
 100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }

}

footer p{
  padding: 0;
  margin: 0;
}

/* responsive code */

@media (max-width:991px){
  #header{
    height:80vh;
    text-align: center;
  }
  #header .header-img{
    text-align: center;
  }
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



/* card css */


.box{
 width: 430px;
 height:430px;
 
 background: #fefff0;
 box-shadow: 5px 5px 10px #666660,-5px 5px 10px #fefff0;
}
.cardine{
 border-top-right-radius: 48%;
 border-bottom-left-radius: 48%;
}
.color{
 width:120px;
 height:120px;
 background: rgba(146,7,238,0.74);
 box-shadow: 5px,5px, 10px, #bbbbb2,-8px -8px 10px #ffffff;
 border-bottom-right-radius: 90px;
 border-top-left-radius: 5px;
 position:-webkit-sticky;
 position:sticky;
}
.circle{
width:105px;
height:105px;
border-radius: 50%;
background: #fefff0; 

box-shadow: 5px 5px 10px #7c56a7f6,-5px -5px 10px #f3f0f0;
position: relative; 
top: -18px;
left:-18px;
}
.circle h1{
   text-align: center;
   padding-top: 30px;
   color:rgba(146,7,238, 0.74);
}

    
.card-img-top{
 width:200px;
 height:180px;
 
}

.effect-1:hover{ 
 background-color: rgba(146,7,238, 0.74);
 color: white;
 cursor:pointer;
}


